* .container{
	max-width: 900px;
}


.head{
	margin-top: 2%;
	line-height: 14px;
}

.logo{
	margin-top: 16px;
}

.img_Capture{
	margin-top: 4%;
    margin-left: 15%;
}

.text_head{
	color: grey;
	margin-left: 50px;
}

.navbar{
	border: 6px ridge #404040;
}

.nav-item a{
	font-size: 20px;
	margin-left: 14px;
	font-family: 'PT Sans Narrow', sans-serif;
	color: white;
	&:hover{
		color: grey;
	}
}


.carousel-content{
	margin-top: 4%;
	float: right;
	line-height: 90px;
}

.carousel-content img{
	margin-top: 4%;
	float: right;
	line-height: 90px;
}


.first_text{
	font-size: 70px;
	color: #b1b1b1;
	font-family: 'PT Sans Narrow', sans-serif;
}

.second_text{
	font-size: 140px;
	font-family: 'PT Sans Narrow', sans-serif;
	font-weight: 700;
	color: #e80099;
}

.order{

 &:before {
  content: "";
  position: absolute;
  bottom: 23px;
  left: 5px;
  width: 140px;
  background-color: grey;
  box-shadow: 0px 0px 10px 1px #000;
  z-index: -1;
	}
}

.carousel-indicators{
	top: 450px;
}

#carouselExampleIndicators .active{

	

	&:after{
	  bottom: 0;		
	  width: 10px;
	  height: 10px;
	  background-color: #00a5e3;
	}

}

@font-face {
font-family: 'Verdana Regular';
src: url('fonts/VerdanaRegular/Verdana.eot');
src: url('fonts/VerdanaRegular/Verdana.eot?#iefix') format('embedded-opentype'),
url('fonts/VerdanaRegular/Verdana.woff') format('woff'),
url('fonts/VerdanaRegular/Verdana.ttf') format('truetype');
font-weight: normal;
font-style: normal;
}


@font-face {
font-family: 'Verdana Italic';
src: url('fonts/VerdanaItalic/Verdana-Italic.eot');
src: url('fonts/VerdanaItalic/Verdana-Italic.eot?#iefix') format('embedded-opentype'),
url('fonts/VerdanaItalic/Verdana-Italic.woff') format('woff'),
url('fonts/VerdanaItalic/Verdana-Italic.ttf') format('truetype');
font-weight: normal;
font-style: normal;
}

